.search-form {
  text-align: center;
}
.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.card-list {
  height: 450px;
  width: 350px;

  list-style: none;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 0px 0px 4px 4px;
  margin-right: 30px;
  margin-bottom: 30px;
}
.card-list:nth-child(4n + 4) {
  margin-right: 0px;
}
.photo-card {
  height: 250px;
}
.photo-card > img {
  display: block;
  max-width: 100%;
  height: 100%;
  object-fit: cover;
}
.stats {
  padding-left: 30px;
}
